<template>
  <div class="flex h-full min-h-[400px] w-full flex-col">
    <GeneralForm
      :heading="'Add New Variable Price'"
      :showCloseButton="true"
      @close-pressed="handleClose"
      @form-submitted="handleSubmit"
      class="flex-1"
    >
      <template #form-body>
        <div class="flex flex-col gap-spacing-100">
          <GeneralSelect
            label="Variable"
            :options="selectOptions"
            @option-selected-full="handleVariableSelect"
          />
          <FormInput
            ref="price"
            label="Price"
            v-model="price"
            :input-id="`price`"
            :field-validations="fieldValidations?.price"
            input-type="number"
          />
          <FormInput
            ref="dateAdded"
            label="Date"
            v-model="dateAdded"
            :input-id="`dateAdded`"
            :field-validations="fieldValidations?.dateAdded"
            input-type="date"
          />
        </div>
      </template>
    </GeneralForm>
  </div>
</template>

<script>
import { FormValidationRule } from "@/utilities/helpers/formValidations";
import { getDateFromToday } from "@/utilities/helpers/utils";

export default {
  props: {
    selectOptions: {
      type: Array,
      required: true,
    },
    product: {
      type: Object,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
  },
  components: {
    FormInput: () => import("@/components/General/FormInput.vue"),
    GeneralForm: () => import("@/components/General/Form.vue"),
    GeneralSelect: () => import("@/components/General/Select.vue"),
  },
  data() {
    return {
      variable: null,
      price: "",
      dateAdded: new Date().toISOString().substring(0, 10),
      fieldValidations: {
        price: new FormValidationRule({
          min: 1,
          required: true,
        }),
        dateAdded: new FormValidationRule({
          type: "DATE",
          required: true,
          minDate: getDateFromToday(-2),
          maxDate: getDateFromToday(2),
        }),
      },
    };
  },
  methods: {
    handleSubmit() {
      const isValid = this.$validateForm();
      if (!isValid) {
        return;
      }
      if (!this.variable?.id) {
        return this.$toast.warning("Please select a variable");
      }
      this.$bus.$emit("product-variable-selected-new-price", {
        index: this.index,
        variable: this.variable,
        product: this.product,
        dateAdded: this.dateAdded,
        price: this.price,
      });
      this.handleClose();
    },
    handleVariableSelect(variable) {
      this.variable = {
        id: variable?.key,
        name: variable?.value,
      };
    },
    handleClose() {
      this.$bus.$emit("select-product-variable-modal", false);
    },
  },
};
</script>
